import React, { Component } from 'react';
import '../InputElement/InputElement.scss';
class InputElement extends Component {
  render() {
    const disabled =
      this.props.disabled && typeof this.props.disabled === 'boolean'
        ? true
        : false;
    const readOnly =
      this.props.readOnly && typeof this.props.readOnly === 'boolean'
        ? true
        : false;
    return this.props.value !== undefined ? (
      <input
        value={this.props.value}
        type={this.props.type}
        id={this.props.id}
        className={'input-style ' + this.props.className}
        readOnly={readOnly}
        disabled={disabled}
        onChange={e => {
          if (
            this.props.onChange &&
            typeof this.props.onChange === 'function'
          ) {
            this.props.onChange(e, this.props.JsonName);
          }
        }}
        onClick={this.props.clickInput}
        onInput={this.props.onInput}
        placeholder={this.props.placeholder}
        onFocus={this.props.focus}
        onBlur={this.props.blur}
        multiple={this.props.multiple}
      />
    ) : (
      <input
        type={this.props.type}
        id={this.props.id}
        className={'input-style ' + this.props.className}
        disabled={disabled}
        onChange={e => {
          if (
            this.props.onChange &&
            typeof this.props.onChange === 'function'
          ) {
            this.props.onChange(e, this.props.JsonName);
          }
        }}
        onClick={this.props.clickInput}
        onInput={this.props.onInput}
        placeholder={this.props.placeholder}
        onFocus={this.props.focus}
        onBlur={this.props.blur}
        multiple={this.props.multiple}
        defaultValue={this.props.defaultValue}
      />
    );
  }
}

InputElement.defaultProps = {
  onChange: () => {},
  onInput: () => {},
  clickInput: () => {},
  focus: () => {},
  blur: () => {},
  className: '',
  id: '',
  placeholder: '',
  type: 'text',
  multiple: false,
  disabled: false,
  defaultValue: '',
};

export default InputElement;
